<!--推荐列表-->
<!--created by qiuwei-->
<template>
  <div class="page-recommend">
    <ul>
      <li class="recommend-panel" v-for="item in recommendData" :key="item.id">
        <a :href="`//item.m.jd.com/product/${item.itemId}.html`">
          <div class="recommend-img">
            <img v-lazy="{src: $supImg(item.mainPic, '300x300') + '!cc_1x1'}"/>
          </div>
          <div class="recommend-text">{{item.title || ''}}</div>
          <div class="recommend-price">
            ￥<span>{{item.price}}</span>
          </div>
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
export default {
  name: 'page-recommend',
  data () {
    return {
      initMap: {
        '江疏影': 113313,
        '张信哲': 113312,
        '鹿晗': 113300,
        '赵丽颖': 113311,
        'other': 156543
      }
    }
  },
  computed: {
    ...mapState('home', [
      'recommendData' // 推荐信息
    ])
  },
  props: {
    recommendKey: {type: String}
  },
  watch: {
    recommendKey (val) {
      if (val) {
        let key = 'other'
        Object.keys(this.initMap).forEach(e => {
          if (val.indexOf(e) !== -1) {
            key = e
          }
        })
        this.getRecommendData(this.initMap[key] || this.initMap.other)
      }
    }
  },
  methods: {
    ...mapActions('home', [
      'getRecommendData',
      'clearRecommendData'
    ])
  },
  destroyed () {
    this.clearRecommendData()
  }
}
</script>

<style lang="scss">
  .page-recommend {
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    & ul {
      white-space: nowrap;
      font-size: 0;
    }
    .recommend-panel {
      display: inline-block;
      background: #fff;
      width: 300px;
      height: 470px;
      box-shadow: 0 12px 30px 0 rgba(255, 52, 52, .05);
      border-radius: 4px;
      overflow: hidden;
      a {
        display: inline-block;
      }
    }
    .recommend-panel + .recommend-panel {
      margin-left: 12px;
    }
    .recommend-panel:nth-child(1) {
      margin-left: 24px;
    }
    .recommend-panel:nth-last-child(1) {
      margin-right: 24px;
    }
    .recommend-img {
      height: 300px;
      background: #efefef;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .recommend-text {
      max-height: 88px;
      padding: 16px 16px 0 16px;
      white-space: pre-wrap;
      word-break: break-all;
      overflow:hidden;
      text-overflow:ellipsis;
      display:-webkit-box;
      -webkit-box-orient:vertical;
      -webkit-line-clamp:2;
      font-size: 28px;
      font-weight: 300;
      line-height: 1.3;
      color: #333;
    }
    .recommend-price {
      font-family: 'jdzh-r';
      padding: 20px 0 0 16px;
      font-size: 24px;
      color: #FF3434;
      span {
        position: relative;
        font-size: 36px;
        top: 1px;
        left: 3px;
        padding-top: 10px;
      }
    }
  }
</style>



