<!--分享页-->
<!--created by qiuwei-->
<template>
  <div class="page-share">
    <!--背景-->
    <div class="top-bac">
      <div class="top-bac--petal"></div>
      <div class="top-bac--petal2"></div>
    </div>
    <!--顶部用信息-->
    <div class="part-head">
      <p class="head-go" @click="$router.push('/home')">
        <span>前往活动主场</span>
      </p>
      <p class="head-img">
        <span>
          <img v-lazy="{ src: $supImg(vo.joinUserHeadImg, '96x96'), error: $reqImg('userDefaultGrey.png'), loading: $reqImg('userDefaultGrey.png')}"/>
        </span>
      </p>
      <p class="head-name">{{vo.joinUserName || ''}}</p>
      <p class="head-tip">我在拍拍二手发现了明星闲置，点赞助我免费拿</p>
    </div>
    <!--物品信息-->
    <div class="part-goods" @click="goDetail">
      <div class="goods-head">
        <div class="goods-head-img">
          <img v-lazy="{src: $supImg((data.detailImgs || [])[0], '215x160')}"/>
        </div>
        <div class="goods-head-title">{{data.goodsName || ''}}</div>
        <div class="goods-head-tip">{{data.goodsDesc || ''}}</div>
      </div>
      <div class="goods-bottom">
      <span class="goods-bottom-img">
        <img v-lazy="{src: $supImg(data.starHeadImg, '60x60'), error: $reqImg('userDefaultGrey.png'), loading: $reqImg('userDefaultGrey.png')}"/>
      </span>
        <span class="goods-bottom-name">{{data.starName || ''}}</span>
        <span class="goods-bottom-line" v-if="data.starName && data.starLabel">|</span>
        <span class="goods-bottom-work">{{data.starLabel || ''}}</span>
        <span class="goods-bottom-end" v-if="data.periodStatus === 904"></span>
        <span
          class="goods-bottom-heart"
          :class="{
          'goods-bottom-heart--active': data.periodStatus === 903,
          'goods-bottom-heart--end': data.periodStatus === 904
        }"
        >
        <i></i>
        <jd-integer :value="data.praiseTotalCount || 0"/>
      </span>
      </div>
    </div>
    <!--进度模块-->
    <div class="part-process">
      <!--倒计时-->
      <div class="process-time" v-if="data.periodStatus === 903">
        <i></i>距结束还剩
        <jd-timer :data="data.periodStatus === 902 ? (data.startTime || 0) : (data.endTime || 0)" @endBack="endBack"/>
      </div>
      <!--获胜者-->
      <div class="process-win" v-if="data.periodStatus === 904">
        <span>
          <img v-lazy="{src: $supImg(data.winnerHeadImg, '40x40'), error: $reqImg('userDefaultGrey.png'), loading: $reqImg('userDefaultGrey.png')}"/>
        </span>
        {{data.winnerName || ''}} &nbsp;获得明星{{data.starName || ''}}的闲置好物
      </div>
      <!--按钮-->
      <div class="process-btn shake" :class="{isDisable: btnInfo.isDisable}" @click="btnHandle(btnInfo.code)">
        <span v-if="data.periodStatus">{{btnInfo.text}}</span>
      </div>
      <div class="process-text" v-html="processText"></div>
      <div class="process-bar">
        <span class="bar-text">当前集赞数</span>
        <span class="bar-body">
        <jd-bar
          :percent="barPercent"
          :left-text="String(vo.praiseTotalCount || 0)"
          :right-text="String(data.praiseMaxCount || 0)"
        />
      </span>
        <span class="bar-text">第一名集赞数</span>
      </div>
      <grid class="process-grid" :data="vo" :isLoading="isLoading" ref="grid"/>
    </div>
    <!--推荐-->
    <p class="part-title">
      <i class="star-left"></i>赞助推荐<i class="star-right"></i>
    </p>
    <div class="part-recommend">
      <recommend :recommendKey="recommendKey"/>
    </div>
    <!--底部信息-->
    <p class="part-bottom">
      <span>本次活动解释权属京东旗下拍拍所有</span>
    </p>
    <!--右上角分享-->
    <arrow ref="arrow"/>
    <!--点赞成功-->
    <success ref="success"/>
    <!--获奖填写地址-->
    <userInfo ref="infoDialog" :periodCode="data.periodCode" :userImg="data.winnerHeadImg" @callback="takeCallback"/>
  </div>
</template>

<script>
import { cornerShare, bottomShare, getDeviceType, getUrlParam, toggleLoading, pageWxShare, logSend } from '../../assets/js/util'
import { mapState, mapGetters, mapActions } from 'vuex'
import grid from './grid'
import arrow from './arrow'
import success from './success'
import recommend from '../home/recommend'
import userInfo from '../mine/user-info'
export default {
  name: 'page-share',
  components: { grid, arrow, success, recommend, userInfo },
  data () {
    return {
      isLoading: true, // 数据是否在加载中
      recommendKey: '' // 推荐加载key
    }
  },
  computed: {
    ...mapGetters('share', [
      'data', // 详情信息
      'vo' // 集赞信息
    ]),
    ...mapState('common', [
      'wxInfo', // 用户微信信息
      'jdInfo' // 用户京东信息
    ]),
    // 进度条百分比（30%-70%）
    barPercent () {
      let left = Number(this.vo.praiseTotalCount || 0)
      let right = Number(this.data.praiseMaxCount || 0)
      if (left === 0 && right === 0) {
        return 50
      } else if (left >= right) {
        return 100
      } else {
        let num = Math.floor((1 - (right - left) / right) * 40)
        num = Math.min(Math.max(num + 30, 30), 70)
        return num
      }
    },
    // 进度条上方文案
    processText () {
      let text = ''
      let rest = Math.max(0, (this.data.praiseMaxCount - this.vo.praiseTotalCount) || 0)
      if (this.data.participated) {
        // 我的视角
        text = Number(rest) === 0 ? '已经是第一名了哦' : `距第一名差<span>${rest}</span>个赞`
      } else {
        // 好友视角
        if (this.vo.supported) {
          // 已助力
          text = `已给好友送了<span>${this.vo.probability || 0}</span>个赞～`
        } else {
          // 未助力
          text = this.data.periodStatus === 904 ? '' : (Number(rest) === 0 ? '已经是第一名了哦' : `距第一名差<span>${rest}</span>个赞`)
        }
      }
      return text
    },
    // 按钮文案
    btnInfo () {
      let text = ''
      let code = ''
      let isDisable = false
      if (this.data.participated) {
        // 我的视角
        text = this.data.periodStatus === 904 ? '活动已结束' : '分享集赞  免费得好物'
        code = this.data.periodStatus === 904 ? 'over' : 'share'
        isDisable = this.data.periodStatus === 904
      } else {
        // 好友视角
        if (this.vo.supported) {
          // 已助力
          text = '我也要参加'
          code = 'join'
        } else {
          // 未助力
          text = this.data.periodStatus === 904 ? '我也要参加' : '给Ta点赞'
          code = this.data.periodStatus === 904 ? 'join' : 'zan'
        }
        isDisable = false
      }
      return {code: code, text: text, isDisable: isDisable}
    }
  },
  created () {
    document.body.style.background = 'transparent'
    document.body.scrollTop = 0
    // pv上报
    setTimeout(() => {
      logSend({
        page_title: '明星闲置分享助力页',
        activityId: getUrlParam('activityInfoId')
      })
    }, 500)
  },
  async mounted () {
    // 微信进入即需要校验授权
    if (getDeviceType() === 'wx') {
      await this.judgeWxIn()
    }
    // 如果是从短信过来领奖的需要京东登录
    if (getUrlParam('from') === 'message') {
      await this.judgeJdIn()
    }
    // 获取页面信息
    await this.getDetail({wxInfo: this.wxInfo})
    this.isLoading = false
    // 如果是从微信详情页点分享过来的 需要提示用户分享在右上角
    this.fromWxDetailJudge()
    let imgUrl = location.protocol + this.$supImg((this.data.detailImgs || [])[0], '90x90')
    // 拍拍App显示右上角分享
    cornerShare(imgUrl)
    // 微信分享图文控制
    pageWxShare({pic: imgUrl})
    // 推荐信息读取
    this.recommendLoad()
    // 如果已京东登录 且获奖 且未领 则弹领取框
    this.judgeTake()
    // 安卓的微信分享主图兼容（微信抓取页面上第一个尺寸大于300的图片作为分享主图）
    this.andrShare()
  },
  methods: {
    ...mapActions('common', [
      'setJdInfo', // 获取京东pin
      'judgeWxIn', // 判断用户是否已微信授权登录 没有则唤起授权登录
      'judgeJdIn' // 判断用户是否已京东登录 没有则跳登录页
    ]),
    ...mapActions('share', [
      'clearDetail', // 清空页面信息
      'getDetail', // 分享页获取页面信息
      'zanAct' // 用户点赞
    ]),
    // 按钮点击
    async btnHandle (status) {
      if (status === 'over') {
        // 活动已结束
        return
      }
      if (status === 'share') {
        // 分享集赞  免费得好物
        setTimeout(() => {
          let shareUrl = `/c2c/star/share?activityInfoId=${getUrlParam('activityInfoId')}&joinPin=${getUrlParam('joinPin')}`
          let imgUrl = `${location.protocol}//img20.360buyimg.com/n1/s215x160_${(this.data.detailImgs || [])[0]}`
          if (getDeviceType() === 'paipaiApp' || getDeviceType() === 'app') {
            bottomShare(shareUrl, imgUrl)
          } else {
            this.showArrow(true)
          }
        }, 200)
      } else if (status === 'join') {
        try {
          let joinPin = getUrlParam('joinPin').replace(/ /, '+')
          let activityInfoId = getUrlParam('activityInfoId')
          // 参加事件上报
          if (!this.jdInfo) {
            await this.setJdInfo()
          }
          logSend({
            eventId: 'paipai_1536564000120|6',
            loginPin: this.jdInfo,
            invitePin: joinPin,
            activityId: activityInfoId
          }, 'click')
        } catch (err) {}
        // 我也要参加
        this.$router.push('/home')
      } if (status === 'zan') {
        if (getDeviceType() === 'wx' && !(await this.judgeWxIn())) {
          // 点赞是微信但没授权的话需要授权
          return
        }
        if (getDeviceType() !== 'wx' && !(await this.judgeJdIn())) {
          // 点赞不是微信的话需要京东登录
          return
        }
        // 给Ta点赞
        toggleLoading(true)
        if (await this.zanAct(this.wxInfo)) {
          this.showSuccess()
          toggleLoading(false)
          await this.getDetail({wxInfo: this.wxInfo, notClear: true})
          this.$refs.grid.showList(false)
          this.$nextTick(() => { this.$refs.grid.showList(true) })
        }
        toggleLoading(false)

        // 点赞事件上报
        await this.setJdInfo()
        logSend({
          eventId: 'paipai_1536564000120|5',
          loginPin: this.jdInfo,
          invitePin: getUrlParam('joinPin').replace(/ /, '+'),
          activityId: getUrlParam('activityInfoId')
        }, 'click')
      }
    },
    // 如果是从微信详情页点分享过来的 需要提示用户分享在右上角
    fromWxDetailJudge () {
      if (getDeviceType() === 'wx' && getUrlParam('from') === 'detail') {
        this.showArrow()
      }
    },
    // 右上角提示点击分享
    showArrow (btnCLick) {
      if (!btnCLick && !this.wxInfo) { // 点击触发 | 微信详情跳分享、有微信信息 这两种都显示
        return
      }
      // 去掉from参数 防止后面重复出现提示
      history.replaceState(null, null, location.href.replace(/(&from)=\w+/gi, ''))
      this.$refs.arrow.shareOpen()
    },
    // 显示点赞成功
    showSuccess () {
      this.$refs.success.show()
    },
    // 如果已京东登录 且获奖 且未领 则弹领取框
    judgeTake () {
      if (this.vo && this.vo.prized && !this.vo.received) {
        this.$refs.infoDialog.show = true
        // forbidScroll(true)
      }
    },
    // 获奖领取完后回调刷页面
    takeCallback () {
      this.getDetail({wxInfo: this.wxInfo, notClear: true})
    },
    // 去详情页
    goDetail () {
      this.$router.push(`/detail?periodCode=${this.data.periodCode}`)
    },
    // 倒计时结束
    endBack () {
      this.getDetail({wxInfo: this.wxInfo, notClear: true})
    },
    // 传入商品标题加载推荐
    recommendLoad () {
      this.recommendKey = this.data.goodsName
    },
    // 安卓的微信分享主图兼容（微信抓取页面上第一个尺寸大于300的图片作为分享主图）
    andrShare () {
      if (/(Android)/i.test(navigator.userAgent) && getDeviceType() === 'wx') {
        setTimeout(() => {
          try {
            let panel = document.querySelector('.page-share')
            let imgOpacity = new Image()
            imgOpacity.src = this.$supImg((this.data.detailImgs || [])[0], '310x310')
            imgOpacity.style.cssText = 'position: fixed; top:0; width: 0;height: 0;opacity: 0;'
            panel.insertBefore(imgOpacity, panel.childNodes[0])
          } catch (err) {}
        }, 1000)
      }
    }
  },
  destroyed () {
    // 页面离开清空信息
    this.clearDetail()
  }
}
</script>

