      color: #fff;
      text-align: center;
      padding: 20px 0 40px;
      .star-left, .star-right {
        display: inline-block;
        width: 75px;
        height: 28px;
        background: url(../../assets/img/star@3x.png) no-repeat center;
        background-size: auto 100%;
        vertical-align: -3px;
        margin: 0 10px;
      }
      .star-left {
        transform: rotate(180deg);
      }
    }
    /*推荐列表模块*/
    .part-recommend {
      transform: translateZ(0);
      width: 100%;
    }
    /*底部信息*/
    .part-bottom {
      position: relative;
      font-size: 24px;
      color: #9f0022;
      margin: 50px 0;
      text-align: center;
      opacity: .5;
      & > span {
        position: relative;
        &:before, &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: -65px;
          height: 1PX;
          width: 50px;
          background: linear-gradient(to right, transparent 0%, #9F0022 100%);
        }
        &:after {
          left: auto;
          right: -65px;
          background: linear-gradient(to left, transparent 0%, #9F0022 100%);
        }
      }
    }
  }
  /*安卓下一些特殊处理*/
  .underAndroid {
    .page-share {
      .jd-timer-item {
        padding-top: 14px !important;
      }
    }
  }
</style>



