<!--点赞成功页-->
<!--created by qiuwei-->
<template>
  <div class="page-success">
    <div class="success-bac" v-if="bacShow"></div>
    <div class="success-body" :class="{'isHide': !bodyShow}">
      <div class="success-panel" :class="{'isHide': !successShow}">
        <div class="success-img">
          <img :src="$reqImg('zansuccess@3x.png')"/>
        </div>
        <div class="success-close" @click="successClose"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { forbidScroll } from '../../assets/js/util'
import BombHeart from '../../assets/js/bomb-heart'
export default {
  name: 'page-success',
  data () {
    return {
      bomb: new BombHeart(), // 爆炸效果
      bacShow: false, // 最底层蒙层
      bodyShow: false, // 是否显示心容器部分 用z-index=-1控制是为了预加载img
      successShow: false // 真正的心显示（动画部分）
    }
  },
  methods: {
    show () {
      this.bomb.showHeart({x: window.innerWidth / 2 - 20, y: 200})
      this.bacShow = true
      this.bodyShow = true
      setTimeout(() => {
        this.successShow = true
        forbidScroll(true)
      }, 100)
    },
    successClose () {
      this.bacShow = false
      this.successShow = false
      forbidScroll(false)
      setTimeout(() => {
        this.bodyShow = false
        this.bomb.removePanel()
      }, 400)
    }
  }
}
</script>
<style type="text/scss" rel="stylesheet/scss" lang="scss">
  .page-success {
    .success-bac {
      position: fixed;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background: rgba(0, 0, 0, .6);
      z-index: 90;
    }
    .success-body {
      position: fixed;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      z-index: 999;
      &.isHide {
        z-index: -1 !important;
      }
    }
    .success-panel {
      position: fixed;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      transition: all ease .3s;
      transform: perspective(1500px) translate3d(0, 0, 0);
      opacity: 1;
      &.isHide {
        transform: perspective(1500px) translate3d(0, 0, 200px);
        opacity: 0;
      }
    }
    .success-img {
      margin: 160px auto 0;
      width: 595px;
      height: 640px;
      img {
        width: 595px;
        height: 640px;
      }
    }
    .success-close {
      width: 65px;
      height: 65px;
      background: url(../../assets/img/close@3x.png) no-repeat center;
      background-size: 100% auto;
      margin: 50px auto 0;
    }
  }
</style>



