<!--微信右上角分享页-->
<!--created by qiuwei-->
<template>
  <div class="page-arrow">
    <transition name="tra-share">
      <div class="share-bac" v-if="shareShow" @click="shareClose">
        <div class="share-panel">
          <svg class="share-arrow" ref="mysvg"></svg>
          <p class="share-p1">
            <i class="share-heart"></i>
            分享集赞，免费得明星好物
            <i class="share-gift"></i>
          </p>
          <p class="share-p2">点击<span>右上角</span>，邀好友集赞吧</p>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import { forbidScroll } from '../../assets/js/util'
export default {
  name: 'page-arrow',
  data () {
    return {
      shareShow: false
    }
  },
  methods: {
    loadSnap () {
      if (!document.querySelectorAll('script[src="//paipai.m.jd.com/c2c/star/static/s-js/snap.svg-min.js"]').length) {
        let scri = document.createElement('script')
        scri.setAttribute('src', '//paipai.m.jd.com/c2c/star/static/s-js/snap.svg-min.js')
        scri.onload = () => {
          setTimeout(() => { this.loadSvg() }, 300)
        }
        document.head.appendChild(scri)
      } else {
        setTimeout(() => { this.loadSvg() }, 300)
      }
    },
    loadSvg () {
      let g = window.Snap('.share-arrow')
      g.attr({ width: 600, height: 500, viewBox: [0, 0, 600, 500] })
      window.Snap.load(this.$reqImg('arrow.svg'), function (f) {
        let line = f.select('#li')
        let arrow = f.select('#ar')
        g.add(line)
        g.add(arrow)

        line.attr({ display: 'none' })
        arrow.attr({ display: 'none' })

        let flight = g.path().attr({
          fill: 'none',
          stroke: '#fff',
          strokeWidth: 8,
          strokeLinecap: 'round',
          strokeLinejoin: 'round',
          strokeDasharray: '8 22'
        }).insertBefore(arrow)

        let arrowShow = false
        let len = window.Snap.path.getTotalLength(line.attr('d'))
        window.Snap.animate(10, len - 10, function (l) {
          if (!arrowShow) {
            arrowShow = true
            arrow.attr({ display: '' })
          }
          let dot = line.getPointAtLength(l)
          flight.attr({ d: line.getSubpath(0, l) })
          arrow.attr({ transform: 't' + [dot.x, dot.y] + 'r' + (dot.alpha - 135) })
        }, 1000)
      })
    },
    shareOpen () {
      this.shareShow = true
      forbidScroll(true)
      setTimeout(() => {
        this.$refs.mysvg.innerHTML = ''
        this.loadSnap()
      }, 0)
    },
    shareClose () {
      this.shareShow = false
      forbidScroll(false)
    }
  }
}
</script>
<style type="text/scss" rel="stylesheet/scss" lang="scss">
  .page-arrow {
    .share-bac {
      position: fixed;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      z-index: 9;
      background: rgba(0, 0, 0, .6);
      .share-panel {
        position: absolute;
        top: 0;
        left: 50%;
        width: 100%;
        height: 100%;
        max-width: 900px;
        transform: translateX(-50%);
      }
      .share-arrow {
        position: absolute;
        top: 20px;
        right: 100px;
        width: 300px;
        height: 250px;
      }
      .share-p1 {
        font-size: 36px;
        color: #fff;
        text-align: center;
        margin-top: 230px;
        .share-heart {
          position: relative;
          top: 20px;
          display: inline-block;
          width: 60px;
          height: 90px;
          background-image: url(../../assets/img/shareHeart@3x.png);
          background-repeat: no-repeat;
          background-size: 100% auto;
        }
        .share-gift {
          position: relative;
          top: 30px;
          left: 5px;
          display: inline-block;
          width: 60px;
          height: 90px;
          background-image: url(../../assets/img/gift@3x.png);
          background-repeat: no-repeat;
          background-size: 100% auto;
        }
      }
      .share-p2 {
        font-size: 48px;
        font-weight: bolder;
        color: #fff;
        text-align: center;
        margin-top: 20px;
        span {
          color: #ffd491;
        }
      }
    }
    /*分享过渡动画*/
    .tra-share-leave-active {
      transition: all ease .3s;
    }
    .tra-share-leave-to {
      opacity: 0;
    }
  }
</style>



