<!--集赞榜单-->
<!--created by qiuwei-->
<template>
  <div class="page-grid page-grid--roll">
    <div class="grid-title">集赞榜单</div>
    <div class="grid-head">
      <div class="head-part"><i></i>点赞好友：{{supportorTotalCount || 0}}人</div>
      <div class="head-part"><i></i>集赞数：{{praiseTotalCount || 0}}个</div>
    </div>
    <div class="grid-list">
      <div class="list-empty" v-if="!isLoading && list.length === 0">
        <span>暂时还没有好友点赞哦～</span>
      </div>
      <transition-group name="list" tag="p" v-if="listShow">
        <p v-for="item in list" :key="JSON.stringify(item)" class="list-item">
          <span class="item-img">
            <img v-lazy="{src: $supImg(item.supportorHeadImg, '50x50'), error: $reqImg('userDefaultGrey.png'), loading: $reqImg('userDefaultGrey.png')}"/>
          </span>
          <span class="item-name">{{item.supportorName || '***'}}</span>
          <span class="item-info">送了{{item.probability || 1}}个赞</span>
          <span class="item-time" v-if="item.time">{{item.time}}前</span>
        </p>
      </transition-group>
    </div>
  </div>
</template>

<script>
import {timeTransform} from '../../assets/js/util'
export default {
  name: 'page-grid',
  data () {
    return {
      listShow: true,
      supportorTotalCount: 0,
      praiseTotalCount: 0,
      list: [],
      currentIndex: 4
    }
  },
  props: {
    data: {
      type: Object,
      default: () => {}
    },
    isLoading: {
      type: Boolean,
      default: true
    }
  },
  watch: {
    'data.supportDetailList' (val) {
      setTimeout(() => {
        this.supportorTotalCount = this.data.supportorTotalCount
        this.praiseTotalCount = this.data.praiseTotalCount
      }, 0)
      if (val && val.length > 0) {
        let now = (new Date()).getTime()
        this.data.supportDetailList.forEach(e => {
          let res = timeTransform(now - e.createAt)
          let hour = 24 * (res.day || 0) + (Number(res.hour) || 0)
          let minute = Number(res.minute)
          let second = Number(res.second)
          if (!hour && !minute) {
            e.time = (second || 1) + '秒'
          } else if (hour) {
            e.time = (hour || 1) + '小时'
          } else if (minute) {
            e.time = (minute || 1) + '分钟'
          }
        })
        if (val.length <= 4) {
          this.list = val
        } else {
          this.list = val.slice(0, 4)
          this.makeClock()
        }
      }
    }
  },
  methods: {
    makeClock () {
      clearInterval(this.clock)
      this.clock = setInterval(() => {
        this.list.shift()
        this.list.push(this.data.supportDetailList[this.currentIndex])
        this.currentIndex = this.currentIndex === this.data.supportDetailList.length - 1 ? 0 : this.currentIndex + 1
      }, 2000)
    },
    showList (show) {
      this.listShow = show
    }
  },
  destroyed () {
    clearInterval(this.clock)
  }
}
</script>

<style lang="scss">
  .page-grid {
    width: 700px;
    margin: 40px auto 30px;
    background: rgba(255, 194, 171, .2);
    border-radius: 8px;
    padding: 0 20px 30px;
    &.page-grid--roll {
      position: relative;
      height: 488px;
      .grid-list {
        position: absolute;
        top: 145px;
        left: 20px;
        width: 660px;
      }
    }
    .grid-title {
      width: 320px;
      height: 64px;
      line-height: 64px;
      font-size: 32px;
      color: #FFEBC9;
      margin: 0 auto;
      text-align: center;
      background: rgba(255, 194, 171, .2);
      border-radius: 0 0 16px 16px;
    }
    .grid-head {
      font-size: 0;
      .head-part {
        font-size: 28px;
        position: relative;
        width: 50%;
        height: 88px;
        line-height: 88px;
        color: #FFEBC9;
        display: inline-block;
        i {
          display: inline-block;
          width: 35px;
          height: 22px;
          background: url(../../assets/img/zan@3x.png) no-repeat center;
          background-size: auto 100%;
          vertical-align: -3px;
          margin-right: 8px;
        }
        &:nth-child(1) {
          &:after {
            content: '';
            position: absolute;
            top: 50%;
            right: 0;
            margin-top: -8px;
            height: 16px;
            border-right: 1px solid #FFEBC9;
          }
          i {
            height: 25px;
            vertical-align: -4px;
            background: url(../../assets/img/friends@3x.png) no-repeat center;
            background-size: auto 100%;
          }
        }
      }
    }
    .grid-list {
      padding: 0 30px;
      overflow: hidden;
      .list-empty {
        position: relative;
        width: 180px;
        height: 160px;
        background: url(../../assets/img/cry@3x.png) no-repeat center;
        background-size: auto 100%;
        margin: 65px auto 80px;
        span {
          position: absolute;
          top: 190px;
          left: 50%;
          transform: translateX(-50%);
          color: #fff;
          font-size: 28px;
          font-weight: 300;
          white-space: nowrap;
        }
      }
      .list-item {
        position: relative;
        font-size: 26px;
        color: #fff;
        line-height: 80px;
        text-align: left;
        .item-img {
          display: inline-block;
          width: 50px;
          height: 50px;
          background: #fff;
          border-radius: 50%;
          vertical-align: middle;
          margin-right: 10px;
          overflow: hidden;
          border: 2px solid #fff;
          img {
            width: 100%;
            height: 50px;
            border-radius: 50%;
          }
        }
        .item-name {
          display: inline-block;
          max-width: 200px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          margin-right: 10px;
          vertical-align: middle;
        }
        .item-info {
          vertical-align: middle;
        }
        .item-time {
          position: absolute;
          top: 0;
          right: 0;
        }
      }
      /*动画*/
      .list-item {
        transition: all 1s;
      }
      .list-enter{
        opacity: 0;
        transform: translateY(100%);
      }
      .list-leave-to {
        opacity: 0;
        transform: translateY(-100%);
      }
      .list-leave-active {
        width: 600px;
        position: absolute;
      }
    }
  }
</style>



