/**
 * 爆炸点赞
 */
function BombHeart (userConfig) {
  // 设备像素比
  this.dpr = document.documentElement.getAttribute('data-dpr') || 1
  // 画布
  this.c = null
  this.ctx = null
  // 碎屑集合
  this.bitArr = []
  // 定时器
  this.clock = null
  // 基础配置
  this.config = Object.assign(
    {
      baseScale: 0.8 * this.dpr, // 基础缩放值
      baseRange: 10 * this.dpr, // 基础抛离距离
      baseNum: 18 // 基础数量
    }, userConfig
  )
}

// 监听点击
BombHeart.prototype.showHeart = function ({x, y}) {
  // 创建画布
  this.addPanel()

  // 清空定时器 清空元素队列
  clearInterval(this.clock)
  this.bitArr = []

  // 随机生成碎屑
  for (let i = 0; i < this.config.baseNum; i++) {
    this.createBit(x, y * this.dpr, this.config.baseScale + 0.5 * Math.random())
  }

  // 不断更新碎屑 直到碎屑缩小没
  this.clock = setInterval(() => {
    if (this.bitArr.length) {
      this.updateBit()
    } else {
      clearInterval(this.clock)
      this.clear()
    }
  }, 10)
}

// 创建画布
BombHeart.prototype.addPanel = function () {
  this.removePanel()
  this.c = document.createElement('canvas')
  this.c.width = window.innerWidth
  this.c.height = window.innerHeight
  this.c.className = 'bombHeart'
  this.c.style.cssText = `position: fixed; top: 0; left: 0; z-index: 95;`
  document.body.appendChild(this.c)
  this.ctx = this.c.getContext('2d')
}

// 创建碎屑数据原型
BombHeart.prototype.createBit = function (x, y, r) {
  let bit = {
    x: x,
    y: y,
    r: r,
    opacity: 1,
    to: {x: 0, y: 0}
  }
  let angle = Math.random() * Math.PI * 2
  bit.to.x = (this.config.baseRange + Math.random() * 5) * Math.cos(angle)
  bit.to.y = (this.config.baseRange + Math.random() * 5) * Math.sin(angle)
  this.bitArr.push(bit)
}

// 更新碎屑数据原型
BombHeart.prototype.updateBit = function () {
  this.clear()
  for (let i = 0; i < this.bitArr.length; i++) {
    let e = this.bitArr[i]
    e.to.x *= 0.94
    e.to.y *= 0.94

    e.to.y += 0.2

    e.x += e.to.x
    e.y += e.to.y
    e.r *= 0.99
    if (e.to.x < 0) {
      e.opacity *= 0.99
    }
    this.drawBit(e)

    // 当体积小于.2时 删除此碎屑
    if (e.r < 0.2) {
      this.bitArr.splice(i, 1)
      i--
      if (this.bitArr.length === 0) {
        this.removePanel()
        clearInterval(this.clock)
      }
    }
  }
}

// 绘制碎屑页面元素
BombHeart.prototype.drawBit = function ({x, y, r, opacity}) {
  // ctx.restore()
  // ctx.beginPath()
  // ctx.arc(x, y, r * 10, 0, 2 * Math.PI)
  // ctx.fillStyle = 'red'
  // ctx.globalAlpha = opacity
  // ctx.fill()
  this.ctx.restore()
  let img = new Image()
  img.src = 'data:image/png;base64,'
  this.ctx.drawImage(img, x, y, 42 * r, 38 * r)
  this.ctx.globalAlpha = opacity
}

// 清空
BombHeart.prototype.clear = function () {
  this.ctx.clearRect(0, 0, this.c.width, this.c.height)
}

// 删除画板
BombHeart.prototype.removePanel = function () {
  let els = document.querySelectorAll('.bombHeart')
  for (let i = 0; i < els.length; i++) {
    els[i].parentNode.removeChild(els[i])
  }
}

export default BombHeart



